.advanced-mode {
  display: none;

  &.active {
    display: block;
  }
}

.simple-mode {
  display: none;

  .constructor-simple {
    position: relative;
    background: #f5f7ff;
    height: calc(100vh - 93px);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .side-left {
    grid-column: 1;
    width: 100%;
    height: 100%;

    .bot-says {
      display: flex;
      justify-content: flex-end;
      position: relative;
      margin-top: 200px;
      margin-right: 50px;
      .btn-bubble-bot {
        border: dashed 1px black;
        background: #fff;
        padding: 15px 20px;
        border-radius: 10px 10px 10px 0;
        cursor: pointer;
      }
      img {
        object-fit: cover;
        margin-right: 5px;
        position: relative;
        top: 45px;
        width: 40px;
        height: 40px;
      }
    }
  }

  .side-right {
    grid-column: 3;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .user-says{
      display: flex;

      margin-top: 150px;
      .btn-bubble-user {
        border: dashed 1px black;
        background: #fff;
        padding: 15px 20px;
        border-radius: 10px 10px 0px 10px;
        cursor: pointer;
        
      }

      img{
        height: 40px;
        position: relative;
        width: 40px;
        top: 50px;
        margin-left: 5px;
      }
    }


    .btn-pdf {

      padding: 5px 10px;
align-self: flex-end;
      background-color: #fff;
      border: solid 1px #d9ded9;
      margin: 0 30px 30px 0;
      border-radius: 5px;
      cursor: pointer;
    }

    
  }

  .chat-creating {
    display: block;
    background: #fff;
    width: 366px;
    height: 100%;
    max-height: calc(100% - 40px);
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    border-radius: 5px;
    padding: 20px 10px;
    grid-column: 2;
    justify-self: center;
    align-self: center;
    overflow-y: auto;

    &__container-msg {
      display: flex;
      margin-top: 20px;
      flex-direction: column;

      .message {
        display: flex;
        width: 100%;
        max-width: 75%;
        margin: 5px 0;
      }
      .message-img {
        width: 25px;
        height: 25px;
        align-self: flex-end;
        img {
          width: 25px;
          height: 25px;
          object-fit: cover;
        }
      }

      .message-bubble {
        padding: 10px 15px;
        white-space: pre-line;
        word-break: break-all;
        border-radius: 10px;
      }

      &.left {
        justify-content: flex-start;

        .message {
          .message-img {
            margin-right: 10px;
          }

          &-bubble {
            border: 1px solid #d9dae1;
            white-space: pre-line;
            word-break: break-all;
            &:last-child {
              border-radius: 10px 10px 10px 0;
            }
          }
        }
      }

      &.right {
        align-items: flex-end;

        .message {
          flex-direction: row-reverse;
          .message-img {
            margin-left: 10px;
          }
          &-bubble {
            border: 1px solid #d9dae1;
            &:last-child {
              border-radius: 10px 10px 0px 10px;
            }
          }
        }
      }
    }

    &--wpp {
      background: url("../../build/images/background-wpp.jpg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      .right {
        .message-bubble {
          background-color: #dcf8c6;
        }
      }
      .left {
        .message-bubble {
          background-color: #fff;
        }
      }

      // &.active {
      //   display: block;
      // }
    }

    &--webchat {
      // &.active {
      //   display: block;
      // }
    }

    &--voice {
      // &.active {
      //   display: block;
      // }
    }
    &--messenger {
      .right {
        .message-bubble {
          background-color: #3578e5;
          color: #fff;
          border: 1px solid #3578e5;
        }
      }
      .left {
        .message-bubble {
          background-color: #f1f0f0;
          border: 1px solid #f1f0f0;
        }
      }
      // &.active {
      //   display: block;
      // }
    }
  }

  &.active {
    display: block;
  }
}
