.box-integrations {
  width: 218px;
  min-height: 120px;
  max-height: 132px;
  height: 100%;
  border-radius: 10px;
  border: solid 1px #d9dae1;
  background-color: #fff;
  position: absolute;
  left: 20%;
  top: 20%;
  cursor: grab;
  &__header {
    padding-bottom: 7px;
    font-family: "Inter Regular";
    height: 40px;
    font-size: 11px;
    font-weight: 600;
    color: #828292;
    border-bottom: solid 1px #d9dae1;
    position: relative;
    display: flex;
    justify-content: center;
    &__top {
      position: absolute;
      width: calc(100% - 18px);
      background: #fff;
      bottom: 8px;
      border-radius: 3px;
      padding-top: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        height: 18px;
        width: 18px;
        position: relative;
        right: 42px;
        path {
          fill: #828292;
        }
      }
    }
  }

  &__main {
    position: relative;
    height: calc(100% - 40px);
    &__connector {
      width: 28px;
      height: 28px;
      border-radius: 4px 0 0 4px;
      background-color: #e9eaef;
      position: absolute;
      bottom: 10px;
      right: 0;
      cursor: pointer;
    }
  }

  &--status-start {
    border: solid #45c777 1px;
    .box-status {
      background: #45c777;

    }
  }

  &--status-integration {
    border: solid #a14dec 1px;
    .box-status {
      background: #a14dec;
      
    }
  }

  &--status-location {
    border: solid #53ecca 1px;
    .box-status {
      background: #53ecca;
      
    }
  }
}

.box-status {
  width: 100%;
  height: 20px;
  border-radius: 9px 9px 0px 0px;
}
