.main {
  height: 100%;
  &__header {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;

    &__title {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__back {
        border-radius: 5px;
        height: 40px;
        width: 40px;
        margin-right: 20px;
        background: $primary;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        padding-left: 5px;

        .material-icons {
          font-size: 16px;
        }
        @include clickable();
      }
    }

    &__btn {
      display: flex;
      justify-content: flex-end;

      &__item {
        &:first-child {
          margin-right: 10px;
        }

        &--fluxo {
          width: 320px;
          height: 40px;
          border-radius: 4.5px;
          border: solid 1px #bcbfd0;
        }
      }
    }
  }

  &__body {
    flex-direction: column;
    // align-items: center;
    justify-content: center;
    width: 100%;

    &__empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 70vh;

      &__img {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        img,
        svg {
          width: 275px;
        }
      }

      &__btn {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        margin-top: 20px;
      }
    }
  }

  .hidden {
    display: none;
  }

  .visible {
    display: grid;
  }
}

.constructor {
  background: #f5f7ff;
  display: none;
  position: relative;
  height: calc(100vh - 93px);
  justify-content: space-between;

  &__content {
    display: none;
    flex-direction: column;
    position: relative;
    &__map {
      display: flex;
      align-items: center;

      margin-top: 20px;
      .list-drop__close__icon {
        margin-top: 4px;
        svg {
          use {
            fill: #828292;
          }

          transform: rotate(272deg);
        }
      }

      .constructor__content__map__text {
        font-size: 12px;
        font-weight: 600;
        color: #828292;
      }
    }

    .btn-add-block {
      display: flex;
      align-items: center;
      max-width: 151px;
      height: 40px;
      font-size: 14px;
      margin-top: 10px;
    }

    &__btn {
      position: relative;
      display: flex;
      align-items: center;
      color: $dark-3 !important;
      justify-content: space-between;

      .btn-icon {
        margin-top: 5px;
      }
    }

    &.active {
      display: flex;
    }
  }

  &__nav {
    display: flex;
    width: 100%;
    justify-content: center;
    height: 40px;
    margin-top: 21px;

    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 87px;
      font-size: 14px;
      margin-right: 8px;
      border-radius: 5px;
      color: #828292;
      cursor: pointer;
      height: 40px;
      &:last-child {
        margin-right: 0;
      }

      &.active {
        border: solid 2px #455cc7;
        color: #455cc7;
      }
    }
  }

  &__param-header {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: 30px;

    &__content {
      display: flex;
      align-items: center;
      // justify-content: space-between;

      &__number {
        color: #828292;
        font-weight: 600;
        margin-right: 14px;
      }

      &__close {
        margin: 0 10px 0 3px;
        cursor: pointer;
        i {
          display: flex;
          font-size: 18px;
          color: #adb1c9;
        }
      }
      &__inputs {
        width: 100%;
        display: flex;
        height: 40px;
        border: 2px solid #bcbfd0;
        border-radius: 5px;

        input {
          font-family: "Inter Regular";
          width: 50%;
          border: none;
          padding: 0 8px;
          color: #bcbfd0;
          &:last-of-type {
            border-left: 1px solid #bcbfd0;
          }
        }
      }
    }
    &.active {
      display: flex;
    }
  }

  &__param-body {
    display: none;

    &.active {
      display: flex;
    }
  }
  &__param-params {
    display: none;

    &.active {
      display: flex;
    }
  }
  &__content-menu {
    display: flex;

    &__menu {
      display: none;
      align-items: center;
      width: 20px;
      justify-content: center;
      align-self: center;
      background-color: #fff;
      height: calc(100vh - 93px);
      border-right: 1px solid $light-5;
      cursor: pointer;

      &.active {
        display: flex;
      }

      &__items {
        &__item {
          width: 4px;
          height: 20px;
          border-radius: 3px;
          background: $light-5;
        }
      }
    }

    &__menu-open {
      display: none;
      width: 40px;
      background-color: #fff;
      display: none;
      height: calc(100vh - 93px);
      justify-content: center;
      border-right: 1px solid $light-5;

      &__items {
        &__item {
          padding-top: 16px;
          cursor: pointer;
          svg {
            path {
              fill: #8c8c9a;
            }
          }

          &.active,
          &:hover {
            svg {
              path {
                fill: $primary;
              }
            }
          }

          @include clickable();
        }
      }
      &.active {
        display: flex;
      }
    }
  }

  .title {
    font-family: "Inter Bold";
    font-size: 24px;
  }

  .container-blocks {
    position: relative;
    height: calc(100vh - 93px);
    display: none;
    font-family: "Inter Regular";

    &.active {
      display: flex;
    }

    .sub-title {
      font-size: 16px;
      margin-bottom: 10px;
      color: $dark-3;
    }
    .nav {
      width: 344px;
      height: calc(100vh - 93px);
      display: none;
      border-right: 1px solid $light-5;
      background: #fff;
      padding: 28px 22px;

      .header-block {
        display: flex;
      }

      &--block {
        padding: 0;

        .header-block {
          padding: 28px 22px;

          .title {
            font-family: "Inter Bold";
            font-size: 24px;
          }
        }

        .block-content {
          padding: 0 28px;
          display: flex;
          height: 56px;
          align-items: center;
          cursor: pointer;

          &:hover {
            background-color: $light-6;
          }

          width: 100%;
          &__icons {
            display: flex;
            &__icon {
              margin-right: 12px;
            }
          }
          &__text {
            font-size: 16px;
          }
        }

        .search {
          margin: 16px 22px;
        }
      }

      &--variable {
        padding: 0;

        .header-variable {
          padding: 28px 22px;
          @include borderbottom();
        }

        .variable__creating {
          padding: 27px 16px;
          @include borderbottom();

          &__area-input {
            display: flex;
            align-items: center;
            height: 40px;
            border: solid 1px $light-2;
            border-radius: 5px;
            padding: 0 14px;
            color: $dark-3;
            margin-bottom: 10px;
            &__icon {
              margin-right: 8px;
              margin-top: 3px;

              svg {
                width: 20px;
                height: 20px;
              }
            }
            &__input {
              font-family: "Inter Regular";
              border: none;
              font-weight: normal;
            }
          }

          &__text {
            font-size: 10px;
            color: $dark-3;
            span {
              font-weight: bold;
            }
          }
        }

        .variable__created {
          padding: 27px 16px;

          &__items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(133px, 0.5fr));
            grid-gap: 10px;
            color: $dark-2;
            &__item {
              display: flex;
              align-items: center;
              min-width: 133px;
              height: 40px;
              border-radius: 5px;
              border: solid 1px #e2e2ec;
              background-color: #fbfbff;
              font-size: 14px;
              word-break: break-all;

              cursor: pointer;

              i {
                font-size: 18px;
                margin-top: 2px;
                margin-right: 8px;
                margin-left: 14px;

                @include clickable();
              }
              span {
                color: $primary;
              }
            }
          }
        }
      }

      &--tree {
      }

      &--linear {
      }

      &.active {
        display: block;
      }
    }

    .search {
      margin: 16px 0;
    }

    .opener {
      align-self: center;
      margin-left: 250px;
      width: 4px;
      height: 20px;
      border-radius: 3px;
      background: $light-5;
      cursor: pointer;
      position: absolute;
      right: -10px;
    }
  }

  &__container-start {
    border-left: 1px solid $light-5;
    height: calc(100vh - 100px);
    width: 369px;
    background-color: #fff;
    z-index: 1;
    font-family: "Inter Regular";
    display: none;
    position: relative;
    overflow-y: auto;

    overflow-x: hidden;
    &.active {
      display: block;
    }

    &__header {
      display: flex;
      justify-content: space-between;
      padding: 28px 16px 26px 16px;
      @include borderbottom();

      &__actions {
        display: flex;
        .icon {
          margin-left: 19px;
        }
      }
    }
    &__main {
      padding: 21px 16px 21px 16px;
      display: flex;
      flex-direction: column;
      @include borderbottom();

      &__textarea {
        font-family: "Inter Regular";
        font-size: 14px;
        line-height: 19px;
        width: 100%;
        height: 68px;
        border-radius: 4px;
        border: solid 1px #bcbfd0;
        resize: none;
        padding: 10px 12px;
        color: $dark-4;
      }

      &__text {
        font-family: "Inter bold";
        font-size: 10px;
        letter-spacing: 0;
        margin: 3px 0 0 0;
      }
    }

    &__content {
      padding: 18px 16px 24px 16px;
      @include borderbottom();

      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__text {
          font-size: 12px;
          color: $dark-2;
        }
      }

      &__main {
        display: flex;
        justify-content: center;

        .btn-add-answer {
          position: relative;
          font-family: "Inter Regular";
          display: flex;
          align-items: center;
          padding: 0 14px;
          color: $dark-2;
          font-size: 14px;
          width: 191px;
          height: 40px;
          border-radius: 5px;
          border: solid 1px #e2e2ec;
          background-color: #fbfbff;
          margin-top: 12px;
          @include clickable();

          svg {
            margin-right: 8px;
            width: 20px;
            height: 20px;
          }
        }
      }
      &--object {
        border-bottom: 0;

        .constructor__container-start__content__main {
          justify-content: flex-start;
        }
      }
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      margin-left: 20px;
      border: solid 1px $light-5;
      @include clickable();
      object-fit: contain;

      svg {
        width: 20px;
        height: 20px;
      }

      &--icon-small {
        width: 19px;
        height: 19px;
        svg {
          width: 15px;
          height: 15px;
        }
      }
    }

    &__add-answer {
      display: none;
      width: 338px;
      border-radius: 4px;
      box-shadow: 0 3px 15px 0 rgba(32, 33, 38, 0.12);
      padding: 0 12px;
      padding-bottom: 14px;
      margin-top: 16px;
      position: relative;

      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 14px 0px;

        &__number {
          font-size: 12px;
          color: #828292;
          border-radius: 10px;
          border: 2px solid #f8f9ff;
          text-align: center;
          width: 20px;
          line-height: 18px;
          padding-right: 1px;
          height: 20px;
        }
        &__close {
          cursor: pointer;
          i {
            font-size: 16px;
            display: flex;
          }
        }
      }

      &__input {
        input {
          font-family: "Inter Regular";
          color: #202126;
          width: 100%;
          padding: 8px 12px;
          border-radius: 4px;
          border: #bcbfd0 1px solid;
          margin-bottom: 9px;
        }
      }
      &.active {
        display: block;
      }
    }
    &__conect {
      &__header {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-size: 10px;

        color: $dark-3;
        i {
          display: flex;
          font-size: 14px;
          margin-right: 6px;
        }
      }

      &__select {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        color: #bcbfd0;
        border-radius: 4px;
        border: solid 1px #bcbfd0;
        align-items: center;
        padding: 8px 12px;
        cursor: pointer;
        position: relative;
        transition: all 0.5s;

        &__icon {
          i {
            display: flex;
          }
        }

        &__options {
          // left: calc(50% - 156px);
          height: 0;
          opacity: 0;
          position: absolute;
          top: 41px;
          width: 100%;
          background: #fff;
          padding: 10px 18px;
          box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
          transition: all 0.3s;
          z-index: 9;
          top: 148px;
          width: 314px;
          margin-bottom: 15px;

          &__header {
            display: none;
            align-items: center;
            &__item {
              margin-right: 14px;
              font-size: 12px;
              cursor: pointer;
              color: #828292;
              font-weight: 400;

              &:hover {
                font-weight: 600;
              }
              &.active {
                color: $primary;
                font-weight: 600;
              }
            }
          }
          &__content-fluxos {
            display: none;
            flex-direction: column;

            &__empty {
              display: none;
              padding: 32px 0;
              align-self: center;
              color: #202126;

              &.active {
                display: block;
              }
            }

            &.active {
              display: flex;
            }
          }

          &__content-blocks {
            display: none;
            flex-direction: column;
            &__empty {
              display: none;
              padding: 32px 0;
              align-self: center;
              color: #202126;

              &.active {
                display: block;
              }
            }

            &__add-block {
              align-self: center;
              padding: 25px 0;
              z-index: 9;
              pointer-events: visible;
            }

            &.active {
              display: flex;
            }
          }

          &.active {
            opacity: 1;
            height: auto;

            .constructor__container-start__conect__select__options__header {
              display: flex;
            }
          }
        }
        &.active {
          .constructor__container-start__conect__select__icon {
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  &__add-box {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 2px solid $primary;
    position: absolute;
    right: 37px;
    top: 37px;
    background: #fff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    @include clickable();

    svg {
      width: 24px;
      path {
        fill: $primary;
      }
    }
  }


  .subtitle-conf {
    display: flex;
    justify-content: space-between;
  }

  .btn--secondary {
    display: flex;
    align-self: flex-end;
  }

  &.active{
    display: flex;
  }
}

.input-dashed {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  margin-top: 16px;
  border: 1px dashed #d9dae1;
  height: 40px;
  border-radius: 5px;
  input {
    border: none;
    width: 100%;
    font-family: "Inter Regular";
    display: flex;
    background: #fff;
    font-size: 14px;
    padding: 0px 14px;
    color: $dark-3;
    transition: 0.5s all;
  }
  ::-webkit-input-placeholder {
    color: #d9dae1;
  }

  &__icon {
    margin-right: 5px;
    cursor: pointer;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 20px;
      height: 20px;
    }
  }
}
.btn-add-object {
  font-family: "Inter Regular";
  display: flex;
  align-items: center;
  border: 1px dashed #d9dae1;
  background: #fff;
  height: 40px;
  font-size: 14px;
  border-radius: 5px;
  padding: 0px 14px;
  cursor: pointer;
  color: $dark-2;
  margin-top: 16px;
  transition: 0.5s all;
  i {
    font-size: 18px;
    margin-top: 2px;
  }

  &--small {
    max-width: 120px;
  }

  // &--medium {
  //   max-width: 151px;
  // }
}

.container-select {
  position: relative;

  .select-btn {
    background-color: white;
    border: 1px solid #b7bec5;
    height: 51px;
    width: 100%;
    font-family: "Inter Regular";
    font-size: 14px;
    border-radius: 5px;
    color: #b7bec5;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    -webkit-box-pack: justify;
    justify-content: space-between;
    cursor: pointer;

    i {
      transform: rotate(90deg);
      transition: all 0.5s;
    }

    &.active {
      i {
        transform: rotate(-92deg);
      }
    }

    &--integrations {
      border: dashed 1px #b7bec5;
      height: 40px;
      margin-top: 10px;

      color: #3e3f44;
    }
  }
}


.list-drop {
  display: flex;
  flex-direction: column;

  .folder {
    display: flex;
    align-items: center;
    color: #828292;
    padding: 10px 0;
    cursor: pointer;
    &:hover {
      background: #ccc;
    }

    &__name {
      font-size: 14px;
    }
    &__icon {
      display: flex;
      align-items: center;
      svg {
        margin-right: 10px;
        path {
          fill: #828292;
        }
      }
    }
  }

  &--primary {
    display: flex;
    border-radius: 5px;
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    position: absolute;
    width: 100%;
    z-index: 5;
    background: #fff;
    padding: 10px;
    opacity: 0;
    height: 0px;
    margin-bottom: 20px;
    pointer-events: none;
    transition: 0.5s all;
    .list-drop__item {
      cursor: pointer;

      // &__name-folder {
      //   font-weight: 400;
      //   color: #828292;
      //   font-size: 14px;
      // }
      // &__img {
      //   width: 22px;
      //   height: 22px;
      //   margin-right: 20px;
      //   img {
      //     width: 22px;
      //     height: 22px;
      //   }
      // }
    }

    &.active {
      height: auto;
      opacity: 1;
      pointer-events: all;
    }
  }

  &--secondary {
    display: flex;
    border-radius: 5px;
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    position: absolute;
    width: 100%;
    z-index: 5;
    background: #fff;
    padding: 10px;
    opacity: 0;
    height: 0px;
    margin-bottom: 20px;
    overflow-y: auto;
    pointer-events: none;
    transition: 0.5s all;
    .list-drop__item {
      cursor: pointer;

      &__name-folder {
        font-weight: 400;
        color: #828292;
        font-size: 14px;
      }
      &__img {
        width: 22px;
        height: 22px;
        margin-right: 20px;
        img {
          width: 22px;
          height: 22px;
        }
      }
    }

    &.active {
      height: 300px;
      opacity: 1;
      pointer-events: all;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 0 14px;

    &__sub-item {
      display: flex;
      margin-left: 20px;
    }

    &--column {
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
    }

    &__primary,
    &__secondary {
      display: flex;
      width: 100%;
      padding: 10px;

      &:hover {
        background: rgb(223, 221, 221);
      }
    }
  }

  // &--map-out {
  //   position: absolute;
  //   background: #fff;
  //   border-radius: 0 0 5px 5px;
  //   box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
  //   padding: 10px 0;
  //   height: 0;
  //   opacity: 0;
  //   transition: all 0.5s;
  //   width: 100%;
  //   top: 56px;
  //   .list-drop__item {
  //     margin-bottom: 5px;
  //     height: 0;
  //     cursor: pointer;
  //     &:hover {
  //       background: #e9eaef;
  //     }
  //   }

  //   &.active {
  //     height: auto;
  //     opacity: 1;
  //     .list-drop__item {
  //       height: auto;
  //     }
  //   }
  // }
  

  &--constructor-block {
    display: none;

    .list-drop__close {
      i {
        font-size: 16px;
        display: flex;
        color: $dark-3;
      }
      margin: 5px 0;
      cursor: pointer;
    }
    .list-drop__item {
      margin-left: 5px;
      margin-bottom: 20px;
      svg {
        margin-right: 14px;
        width: 18px;
        height: 18px;
      }
      a {
        display: flex;
        align-items: center;
        color: $dark-3;
        font-size: 14px;
        font-weight: 600;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }

    &.active {
      display: flex;
    }
  }
}
.map-var {
  &__response {
    display: none;

    &.active {
      display: flex;
    }
  }
  &__dataset {
    display: none;

    &.active {
      display: flex;
    }
  }

  &__content {
    display: flex;
    width: 100%;
    margin: 10px 0px;

    pre {
      background: #f5f7ff;
      padding: 11px 10px;
      border-radius: 0px 4px 4px 0px;
      white-space: pre-wrap;
      word-break: break-word;
    }
  }
}

.change-mode {
  display: flex;
  width: 80px;
  height: 40px;
  border-radius: 5px;
  border: solid 2px $primary;
  margin-right: 54px;
  justify-content: space-between;

  &__icon {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
    svg {
      height: 24px;
      width: 24px;
      path {
        fill: $primary;
      }
    }

    &.active {
      background: $primary;
      svg {
        path {
          fill: #fff;
        }
      }
    }
  }
}

.change-social-social {
  display: flex;
  border-radius: 5px;
  box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
  border: solid 1px #f8f9ff;
  height: 40px;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 91px;
    height: 36px;
    border-radius: 3px;
    cursor: pointer;
    font-size: 12px;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: 0.14px;
    border-left: 1px solid #f5f7ff;

    &.active{
      border: solid 1px #e2e2ec;
      background-color: #fbfbff;
      color: $primary;
    }
  }
}

@keyframes upAndDown {
  0%,
  100% {
    margin-bottom: 20px;
  }
  50% {
    margin-bottom: 0px;
  }
}
